import React, { useRef } from 'react'
import Modal from '../Modal'

import { RowBetween } from '../Row'
import { TYPE, CloseIcon } from '../../theme'
import {  NftToken } from '../../state/nft/nftToken'
import {  Separator,PaddedColumn } from './styled'
import CurrencyList from './CurrencyList'
import Column from '../Column'

import AutoSizer from 'react-virtualized-auto-sizer'
import { FixedSizeList } from 'react-window'
import { useTranslation } from 'react-i18next'


interface NftModalProps {
  isOpen: boolean
  onDismiss: () => void
  nftToken: NftToken
}

export default function NftInfoModal({ isOpen, onDismiss, nftToken }: NftModalProps) {


  function wrappedOndismiss() {
    onDismiss()
  }
  const { t } = useTranslation()


  const fixedList = useRef<FixedSizeList>()
  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOndismiss} maxHeight={80} minHeight={ 80}>
      <Column style={{ width: '100%', flex: '1 1' }}>
        <PaddedColumn gap="14px">
      <RowBetween>
          <TYPE.mediumHeader>{t("myNft",{token:'MM'})}</TYPE.mediumHeader>
          <CloseIcon onClick={wrappedOndismiss} />
        </RowBetween>
        </PaddedColumn>

        <Separator />


        <div style={{ flex: '1' }}>
          <AutoSizer disableWidth>
            {({ height }) => (
              <CurrencyList
                height={height}
                currencies={nftToken?.machine??[]}
                onCurrencySelect={()=>{}}
                fixedListRef={fixedList}
              />
            )}
          </AutoSizer>
        </div>
      </Column>
    </Modal>
  )
}
