import React, { CSSProperties, MutableRefObject, useCallback, useMemo } from 'react'
import { FixedSizeList } from 'react-window'
import { Text } from 'rebass'
import styled from 'styled-components'
import { useActiveWeb3React } from '../../hooks'
import {  WrappedTokenInfo } from '../../state/lists/hooks'
import Column from '../Column'
import { RowFixed } from '../Row'
import { MouseoverTooltip } from '../Tooltip'
import { FadedSpan, MenuItem } from './styled'
import Loader from '../Loader'
import { IMachine } from '../../state/nft/nftToken'

function currencyKey(currency: IMachine): number {
  return currency.id
}

const StyledBalanceText = styled(Text)`
  white-space: nowrap;
  overflow: hidden;
  max-width: 5rem;
  text-overflow: ellipsis;
`

const Tag = styled.div`
  background-color: ${({ theme }) => theme.bg3};
  color: ${({ theme }) => theme.text2};
  font-size: 14px;
  border-radius: 4px;
  padding: 0.25rem 0.3rem 0.25rem 0.3rem;
  max-width: 6rem;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  justify-self: flex-end;
  margin-right: 4px;
`

function Balance({ balance }: { balance: number }) {
  return <StyledBalanceText title={"123"}>{balance.toFixed(2)}</StyledBalanceText>
}

const TagContainer = styled.div`
  display: flex;
  justify-content: flex-end;
`

function TokenTags({ currency }: { currency: IMachine }) {
  if (!(currency instanceof WrappedTokenInfo)) {
    return <span />
  }

  const tags = currency.tags
  if (!tags || tags.length === 0) return <span />

  const tag = tags[0]

  return (
    <TagContainer>
      <MouseoverTooltip text={tag.description}>
        <Tag key={tag.id}>{tag.name}</Tag>
      </MouseoverTooltip>
      {tags.length > 1 ? (
        <MouseoverTooltip
          text={tags
            .slice(1)
            .map(({ name, description }) => `${name}: ${description}`)
            .join('; \n')}
        >
          <Tag>...</Tag>
        </MouseoverTooltip>
      ) : null}
    </TagContainer>
  )
}

function CurrencyRow({
  currency,
  onSelect,
  isSelected,
  otherSelected,
  style
}: {
  currency: IMachine
  onSelect: () => void
  isSelected: boolean
  otherSelected: boolean
  style: CSSProperties
}) {
  const { account } = useActiveWeb3React()
  const key = currencyKey(currency)

  const balance = currency.balance??0



  // only show add or remove buttons if not on selected list
  return (
    <MenuItem
      style={style}
      className={`token-item-${key}`}
      onClick={() => (isSelected ? null : onSelect())}
      disabled={isSelected}
      selected={otherSelected}
    >
      <img src={currency?.img??''} style={{width:'50px',height:'30px'}}/>
      <Column>
        <Text title={currency.id.toString()} fontWeight={500}>
          {currency.load}/{currency.mining}
        </Text>

        <FadedSpan>

          {/*{!isOnSelectedList && !customAdded ? (*/}
            {/*<TYPE.main fontWeight={500}>*/}
              {/*Found by address*/}
              {/*<LinkStyledButton*/}
                {/*onClick={event => {*/}
                  {/*event.stopPropagation()*/}
                  {/*if (currency instanceof Token) addToken(currency)*/}
                {/*}}*/}
              {/*>*/}
                {/*(Add)*/}
              {/*</LinkStyledButton>*/}
            {/*</TYPE.main>*/}
          {/*) : null}*/}
        </FadedSpan>
      </Column>
      <TokenTags currency={currency} />
      <RowFixed style={{ justifySelf: 'flex-end' }}>
        {balance ? <Balance balance={balance} /> : account ? <Loader /> : null}
      </RowFixed>
    </MenuItem>
  )
}

export default function CurrencyList({
  height,
  currencies,
  selectedCurrency,
  onCurrencySelect,
  otherCurrency,
  fixedListRef
}: {
  height: number
  currencies: IMachine[]
  selectedCurrency?: IMachine | null
  onCurrencySelect: (currency: IMachine) => void
  otherCurrency?: IMachine | null
  fixedListRef?: MutableRefObject<FixedSizeList | undefined>
}) {
  const itemData = useMemo(() => (currencies), [currencies])

  const Row = useCallback(
    ({ data, index, style }) => {
      const currency: IMachine = data[index]
      const isSelected = Boolean(selectedCurrency && selectedCurrency.id===currency.id)
      const otherSelected = Boolean(otherCurrency && otherCurrency.id===currency.id)
      const handleSelect = () => onCurrencySelect(currency)
      return (
       <CurrencyRow
          style={style}
          currency={currency}
          isSelected={isSelected}
          onSelect={handleSelect}
          otherSelected={otherSelected}
        />
      )
    },
    [onCurrencySelect, otherCurrency, selectedCurrency]
  )

  const itemKey = useCallback((index: number, data: any) => currencyKey(data[index]), [])

  return (
    <FixedSizeList
      height={height}
      ref={fixedListRef as any}
      width="100%"
      itemData={itemData}
      itemCount={itemData.length}
      itemSize={56}
      itemKey={itemKey}
    >
      {Row}
    </FixedSizeList>
  )
}
