import { ChainId, JSBI, Token, TokenAmount } from '@batsoft/swap-sdk'
import { useMemo } from 'react'
import { UNI, CM, LockContract, NFT_MM,Machines } from '../../constants'
import { GAME_INTERFACE } from '../../constants/abis/game'
import { LOCK_INTERFACE } from '../../constants/abis/lock'

import { NFT_INTERFACE } from '../../constants/abis/nft'

import { useActiveWeb3React } from '../../hooks'
import { useMultipleContractSingleData } from '../multicall/hooks'
import { IMachine, NftToken } from './nftToken'


// TODO add staking rewards addresses here
export const STAKING_REWARDS_INFO: {
  [chainId in ChainId]?: {
    token: Token
    nft: NftToken
    stakingRewardAddress: string
    gameAddress:string
  }[]
} = {
  [ChainId.MAINNET]: [
    {
      token: CM,
      nft:NFT_MM[ChainId.MAINNET],
      stakingRewardAddress: LockContract.find(x=>x.token.address===CM.address)?.lockAddress??'',
      gameAddress:LockContract.find(x=>x.token.address===CM.address)?.gameAddress??''
    }
  ],
  [ChainId.ROPSTEN]: [
    {
      token: CM,
      nft:NFT_MM[ChainId.ROPSTEN],
      stakingRewardAddress: LockContract.find(x=>x.token.address===CM.address)?.lockAddress??'',
      gameAddress:LockContract.find(x=>x.token.address===CM.address)?.gameAddress??''

    }
  ]
}

export interface StakingInfo {
  // the address of the reward contract
  stakingRewardAddress: string
  gameAddress: string

  // the tokens involved in this pair
  token: Token
  nft:NftToken,
  // the amount of token currently staked, or undefined if no account
  stakedAmount: TokenAmount
  // the total amount of token staked in the contract
  totalStakedAmount?: TokenAmount
  // user nft total amount
  nftTotalAmount:number

  // 总剩余
  nftPoolTotal:JSBI
  // 可以领取数量
  nftAllowAmount:JSBI
  // 是可以领取1155
  allowReceive:boolean

}

// gets the staking info from the network for the active chain id
export function useStakingInfo(): StakingInfo[] {
  const { chainId, account } = useActiveWeb3React()

  const info = useMemo(
    () =>
      chainId
        ? STAKING_REWARDS_INFO[chainId] ?? []
        : [],
    [chainId]
  )

  const uni = chainId ? UNI[chainId] : undefined

  const rewardsAddresses = useMemo(() => info.map(({ stakingRewardAddress }) => stakingRewardAddress), [info])
  const gameAddress = useMemo(() => info.map(({ gameAddress }) => gameAddress), [info])


  const nftAddresses = useMemo(() => info.map(({ nft }) => nft.address), [info])

  const accountArg = useMemo(() => [account ?? undefined], [account])

  // get all the info from the staking rewards contracts
  const balances = useMultipleContractSingleData(rewardsAddresses, LOCK_INTERFACE, 'users', accountArg)
  //const totalSupplies = useMultipleContractSingleData(gameAddress, GAME_INTERFACE, 'ORE_AMOUNT')

  const nftReceive = useMultipleContractSingleData(gameAddress, GAME_INTERFACE, 'cat', accountArg)

  // nft 可用
  const nftTotal = useMultipleContractSingleData(gameAddress, GAME_INTERFACE, 'determinate')

  // 用户数据相关
  const user = useMultipleContractSingleData(gameAddress, GAME_INTERFACE, 'getPersonalStats',[0,...accountArg])


  console.log("user=====",user)


  let _address=[];
  let ids=[];
  for (let i = 1; i <= 18; i++) {
    ids.push(i);
    _address.push(account ?? '')
  }
  const nftBalances = useMultipleContractSingleData(nftAddresses, NFT_INTERFACE, 'balanceOfBatch',[_address,ids])

  return useMemo(() => {
    if (!chainId || !uni) return []

    return rewardsAddresses.reduce<StakingInfo[]>((memo, rewardsAddress, index) => {
      // these two are dependent on account
      const balanceState = balances[index]

      // these get fetched regardless of account
     // const totalSupplyState = totalSupplies[index]

      const nftBalanceState=nftBalances[index]

      const nftReceiveState=nftReceive[index]

      const nftTotalState=nftTotal[index]

      const userState=user[index]

      if (
        // these may be undefined if not logged in
        !balanceState?.loading &&
        !nftBalanceState?.loading &&
        // always need these
        // totalSupplyState &&
        // !totalSupplyState.loading&&
        nftReceiveState&&
        !nftReceiveState.loading&&
        nftTotalState&&
        !nftTotalState.loading&&
        userState&&
        !userState.loading
      ) {
        if (
          balanceState?.error ||
          nftBalanceState?.error ||
         // totalSupplyState.error||
          nftReceiveState?.error ||
          nftReceiveState.error||
          nftTotalState.error||
          userState.error
        ) {
          console.error('Failed to load staking rewards info')
          return memo
        }

        // get the LP token
        const tokens = info[index].token

        // check for account, if no account set to 0

        const stakedAmount = new TokenAmount(tokens, JSBI.BigInt(balanceState?.result?.[1] ?? 0))
       // const totalStakedAmount = new TokenAmount(tokens, JSBI.BigInt(totalSupplyState.result?.[0]))

        // nft balances
        const totalNftAmount=nftBalanceState.result?.[0]??[];

        const nftPoolTotal=JSBI.BigInt(nftTotalState.result?.['token1155Amount']??0);
        const nftAllowAmount=JSBI.BigInt(nftReceiveState.result?.['quantity']??0);

        // 合约中是是否领取过1155
        const allowReceive=userState.result?.['stats2'][2]

        let machines:IMachine[]=[];
        let nftTotalAmount=0;
        totalNftAmount.map((item:number,index:number)=>{
          let i=index+1;
          let data:any={
            balance: parseInt(item.toString()),
            ...Machines.find((item:IMachine)=>item.id===i)
          };
          machines.push(data);
        })

        machines.map(item=>nftTotalAmount=nftTotalAmount+(item?.balance??0))

        let nftToken=info[index].nft;
        nftToken.machine=machines.filter(item=>item?.balance??0>0);

        memo.push({
          stakingRewardAddress: rewardsAddress,
          gameAddress:info[index].gameAddress,
          token: info[index].token,
          nft:nftToken,
          stakedAmount: stakedAmount,
          nftTotalAmount:nftTotalAmount,
          nftPoolTotal:nftPoolTotal,
          nftAllowAmount:nftAllowAmount,
          allowReceive:allowReceive
        })
      }
      return memo
    }, [])
  }, [balances, chainId, info, rewardsAddresses, uni,nftBalances,nftReceive])
}


