import React, { useState } from 'react'

export function Item({ data, columns,onSelect }: { data: Array<object>,columns:Array<object>,onSelect:(data: any)=>void }) {
  const [active, setActive] = useState(0)
  return (
    <li className="table-body">
      <ul>
        <div className="innerbox body-content">
          {data.map((item: any, index: number) => (
            <li key={index} className={active === item.id ? 'table-body-item is-active' : 'table-body-item'}>
              {Object.keys(item).map(opt => {
                return (
                  columns.map((we:any)=>we.dataIndex).includes(opt) ? <div>{item[opt]}</div>: null
                )
              })}
              <div onClick={() => {
                setActive(item.id)
                onSelect(item)
              }}>
                <div className="out-line">
                  <div className="in-point"></div>
                </div>
              </div>
            </li>
          ))}
        </div>
        <div className="moreData"> 没有更多了~</div>
      </ul>
    </li>
  )
}

function SelectBlock({
  children,
  header,
  className,
  columns,
  data,
  onSelect
}: {
  children?: React.ReactNode
  header: any
  className?: string
  columns: Array<object>
  data: Array<object>
  onSelect:(data: any) => void
}) {
  return (
    <div className={`${className} inlineBlock`}>
      <header className="container-header">
        <div> {header}</div>
      </header>
      <div className="liquidation-project">
        <ul className="table-container">
          <li className="table-header">
            {columns.map((item: any, index: number) => (
              <div key={index}>{item.title}</div>
            ))}
          </li>
          <Item data={data} columns={columns} onSelect={onSelect} />
        </ul>
      </div>
    </div>
  )
}
export default SelectBlock
