import React, { useState } from 'react'
import Modal from '../Modal'
import { AutoColumn } from '../Column'
import styled from 'styled-components'
import { RowBetween } from '../Row'
import { TYPE, CloseIcon } from '../../theme'
import { ButtonError } from '../Button'
import { StakingInfo } from '../../state/nft/hooks'
import { useLockContract,useGameContract } from '../../hooks/useContract'
import { SubmittedView, LoadingView } from '../ModalViews'
import { TransactionResponse } from '@ethersproject/providers'
import { useTransactionAdder } from '../../state/transactions/hooks'
import { useActiveWeb3React } from '../../hooks'
//import { calculateGasMargin } from '../../utils'
import { useTranslation } from 'react-i18next'
import { splitSignature } from 'ethers/lib/utils'
import useTransactionDeadline from '../../hooks/useTransactionDeadline'

const ContentWrapper = styled(AutoColumn)`
  width: 100%;
  padding: 1rem;
`

interface StakingModalProps {
  isOpen: boolean
  onDismiss: () => void
  stakingInfo: StakingInfo
}

export default function ReceiveModal({ isOpen, onDismiss, stakingInfo }: StakingModalProps) {
  const { account, chainId, library } = useActiveWeb3React()
  const { t } = useTranslation()

  // monitor call to help UI loading state
  const addTransaction = useTransactionAdder()
  const [hash, setHash] = useState<string | undefined>()
  const [attempting, setAttempting] = useState(false)

  function wrappedOndismiss() {
    setHash(undefined)
    setAttempting(false)
    onDismiss()
  }
  const deadline = useTransactionDeadline()

  const stakingContract = useLockContract(stakingInfo.stakingRewardAddress)
  const gameContract = useGameContract(stakingInfo.gameAddress)

  async function onWithdraw() {
    if (gameContract&&stakingContract && stakingInfo?.stakedAmount) {


      if (!library || !deadline) throw new Error('missing dependencies')
      // try to gather a signature for permission
      const nonce = await stakingContract._nonces(account)

      const EIP712Domain = [
        { name: 'name', type: 'string' },
        { name: 'version', type: 'string' },
        { name: 'chainId', type: 'uint256' },
        { name: 'verifyingContract', type: 'address' }
      ]
      const domain = {
        name: 'MiningPool',
        version: '1',
        chainId: chainId,
        verifyingContract: stakingContract.address
      }
      const Lock = [
        { name: 'holder', type: 'address' },
        { name: 'locker', type: 'address' },
        { name: 'nonce', type: 'uint256' },
        { name: 'expiry', type: 'uint256' },
        { name: 'allowed', type: 'bool' },
      ]
      const message = {
        holder: account,
        locker: stakingInfo.gameAddress,
        nonce: nonce.toHexString(),
        expiry: deadline.toNumber(),
        allowed: true
      }
      const data = JSON.stringify({
        types: {
          EIP712Domain,
          Lock
        },
        domain,
        primaryType: 'Lock',
        message
      })

      library
        .send('eth_signTypedData_v4', [account, data])
        .then(splitSignature)
        .then(async (signature) => {

          if (stakingContract  && signature) {
            setAttempting(true)
            // const estimatedGas = await gameContract.estimateGas.obtainCar(message.nonce,deadline.toNumber(),true,signature.v,signature.r,signature.s).catch(() => {
            //   // general fallback for tokens who restrict approval amounts
            //   return gameContract.estimateGas.obtainCar(message.nonce,deadline.toNumber(),true,signature.v,signature.r,signature.s)
            // })

            gameContract
              .obtainCar(message.nonce,deadline.toNumber(),true,signature.v,signature.r,signature.s, { gasLimit: 800000  })
              .then((response: TransactionResponse) => {
                addTransaction(response, {
                  summary: `Receive ${stakingInfo.nft.symbol} token`
                })
                setHash(response.hash)
              })
              .catch((error: any) => {
                setAttempting(false)
                console.log(error)
              })

          }

        })
        .catch(error => {
          // for all errors other than 4001 (EIP-1193 user rejected request), fall back to manual approve
          if (error?.code !== 4001) {
            //approveCallback()
          }
        })

      // const estimatedGas = await stakingContract.estimateGas.obtainCar().catch(() => {
      //   // general fallback for tokens who restrict approval amounts
      //   return stakingContract.estimateGas.obtainCar()
      // })
      // await stakingContract
      //   .obtainCar({ gasLimit: calculateGasMargin(estimatedGas) })
      //   .then((response: TransactionResponse) => {
      //     addTransaction(response, {
      //       summary: `Receive ${stakingInfo.nft.symbol} token`
      //     })
      //     setHash(response.hash)
      //   })
      //   .catch((error: any) => {
      //     setAttempting(false)
      //     console.log(error)
      //   })
    }
  }

  let error: string | undefined
  if (!account) {
    error = 'Connect Wallet'
  }
  if (!stakingInfo?.stakedAmount) {
    error = error ?? 'Stake Token'
  }

  return (
    <Modal isOpen={isOpen} onDismiss={wrappedOndismiss} maxHeight={90}>
      {!attempting && !hash && (
        <ContentWrapper gap="lg">
          <RowBetween>
            <TYPE.mediumHeader>{t("reward")}</TYPE.mediumHeader>
            <CloseIcon onClick={wrappedOndismiss} />
          </RowBetween>
          {stakingInfo?.stakedAmount && (
            <AutoColumn justify="center" gap="md">
              <TYPE.body>{t("nft_5")}:</TYPE.body>
              <TYPE.body fontWeight={600} fontSize={36}>
                {stakingInfo.nftAllowAmount}&nbsp;{stakingInfo.nft.symbol}
              </TYPE.body>

            </AutoColumn>
          )}

          <TYPE.subHeader style={{ textAlign: 'center' }}>
            {t("nft_6",{token:stakingInfo.nft.symbol})}
          </TYPE.subHeader>
          <ButtonError disabled={!!error} error={!!error && !!stakingInfo?.stakedAmount} onClick={onWithdraw}>
            {error ?? t("reward")}
          </ButtonError>
        </ContentWrapper>
      )}
      {attempting && !hash && (
        <LoadingView onDismiss={wrappedOndismiss}>
          <AutoColumn gap="12px" justify={'center'}>
            <TYPE.body fontSize={20}>Receiveing  {stakingInfo.nft.symbol}</TYPE.body>
          </AutoColumn>
        </LoadingView>
      )}
      {hash && (
        <SubmittedView onDismiss={wrappedOndismiss} hash={hash}>
          <AutoColumn gap="12px" justify={'center'}>
            <TYPE.largeHeader>Transaction Submitted</TYPE.largeHeader>
            <TYPE.body fontSize={20}>Receive {stakingInfo.nft.symbol}!</TYPE.body>
          </AutoColumn>
        </SubmittedView>
      )}
    </Modal>
  )
}
