import { ChainId, CurrencyAmount, JSBI, Token, TokenAmount } from '@batsoft/swap-sdk'
import { useMemo } from 'react'
import { UNI,CM,LockContract } from '../../constants'
import { LOCK_INTERFACE } from '../../constants/abis/lock'

import { useActiveWeb3React } from '../../hooks'
import { useMultipleContractSingleData } from '../multicall/hooks'
import { tryParseAmount } from '../swap/hooks'
import { GAME_INTERFACE } from '../../constants/abis/game'

export const STAKING_GENESIS = 1600387200

export const REWARDS_DURATION_DAYS = 60
export const UNLOCK=86400;

// TODO add staking rewards addresses here
export const STAKING_REWARDS_INFO: {
  [chainId in ChainId]?: {
    token: Token
    stakingRewardAddress: string,
    gameAddress: string

  }[]
} = {
  [ChainId.MAINNET]: [
    {
      token: CM,
      stakingRewardAddress: LockContract.find(x=>x.token.address===CM.address)?.lockAddress??'',
      gameAddress: LockContract.find(x=>x.token.address===CM.address)?.gameAddress??''
    }
  ],
  [ChainId.ROPSTEN]: [
    {
      token: CM,
      stakingRewardAddress: LockContract.find(x=>x.token.address===CM.address)?.lockAddress??'',
      gameAddress: LockContract.find(x=>x.token.address===CM.address)?.gameAddress??''
    }
  ]
}

export interface StakingInfo {
  // the address of the reward contract
  stakingRewardAddress: string
  // the tokens involved in this pair
  token: Token
  // the amount of token currently staked, or undefined if no account
  stakedAmount: TokenAmount
  // the total amount of token staked in the contract
  totalStakedAmount: TokenAmount
  //允许提币时间
  allowWithdrawTime:number
  // 是否允许赎回
  allowUnlock:boolean

}

// gets the staking info from the network for the active chain id
export function useStakingInfo(): StakingInfo[] {
  const { chainId, account } = useActiveWeb3React()

  const info = useMemo(
    () =>
      chainId
        ? STAKING_REWARDS_INFO[chainId] ?? []
        : [],
    [chainId]
  )

 // const startTime=useLockContract()

  const uni = chainId ? UNI[chainId] : undefined

  const rewardsAddresses = useMemo(() => info.map(({ stakingRewardAddress }) => stakingRewardAddress), [info])
  const gameAddress = useMemo(() => info.map(({ gameAddress }) => gameAddress), [info])


  const accountArg = useMemo(() => [account ?? undefined], [account])

  // get all the info from the staking rewards contracts
  const balances = useMultipleContractSingleData(rewardsAddresses, LOCK_INTERFACE, 'users', accountArg)
  const totalSupplies = useMultipleContractSingleData(rewardsAddresses, LOCK_INTERFACE, 'stakeAmount')

  const startTime = useMultipleContractSingleData(rewardsAddresses, LOCK_INTERFACE, 'ANCHOR')

  // 全局数据相关
  const global = useMultipleContractSingleData(gameAddress, GAME_INTERFACE, 'getGlobalStats',[0])

// 用户数据相关
  const user = useMultipleContractSingleData(gameAddress, GAME_INTERFACE, 'getPersonalStats',[0,...accountArg])


  return useMemo(() => {
    if (!chainId || !uni) return []

    return rewardsAddresses.reduce<StakingInfo[]>((memo, rewardsAddress, index) => {
      // these two are dependent on account
      const balanceState = balances[index]

      // these get fetched regardless of account
      const totalSupplyState = totalSupplies[index]

      const startTimeState=startTime[index]
      const globalState=global[index]

      const userState=user[index]


      if (
        // these may be undefined if not logged in
        !balanceState?.loading &&
        // always need these
        totalSupplyState &&
        !totalSupplyState.loading&&
        startTimeState &&
        !startTimeState.loading&&
        globalState &&
        !globalState.loading&&
        userState&&
        !userState.loading
      ) {
        if (
          balanceState?.error ||
          totalSupplyState.error

        ) {
          console.error('Failed to load staking rewards info')
          return memo
        }

        // get the LP token
        const tokens = info[index].token

        // check for account, if no account set to 0

        const stakedAmount = new TokenAmount(tokens, JSBI.BigInt(balanceState?.result?.[1] ?? 0))
        const totalStakedAmount = new TokenAmount(tokens, JSBI.BigInt(totalSupplyState.result?.[0]))

        const st=parseInt(startTimeState?.result?.[0])
        const step=parseInt(globalState?.result?.["stats"]?.[4])

        const allowUnlock=userState?.result?.['stats2'][2]

        const allowWithdrawTime=st+step

        console.log(allowWithdrawTime)
        memo.push({
          stakingRewardAddress: rewardsAddress,
          token: info[index].token,
          stakedAmount: stakedAmount,
          totalStakedAmount: totalStakedAmount,
          allowWithdrawTime:allowWithdrawTime*1000,
          allowUnlock:!allowUnlock
        })
      }
      return memo
    }, [])
  }, [balances, chainId, info, rewardsAddresses, totalSupplies, uni])
}



// based on typed value
export function useDerivedStakeInfo(
  typedValue: string,
  stakingToken: Token,
  userLiquidityUnstaked: TokenAmount | undefined
): {
  parsedAmount?: CurrencyAmount
  error?: string
} {
  const { account } = useActiveWeb3React()

  const parsedInput: CurrencyAmount | undefined = tryParseAmount(typedValue, stakingToken)

  const parsedAmount =
    parsedInput && userLiquidityUnstaked && JSBI.lessThanOrEqual(parsedInput.raw, userLiquidityUnstaked.raw)
      ? parsedInput
      : undefined

  let error: string | undefined
  if (!account) {
    error = 'Connect Wallet'
  }
  if (!parsedAmount) {
    error = error ?? 'Enter an amount'
  }

  return {
    parsedAmount,
    error
  }
}

// based on typed value
export function useDerivedUnstakeInfo(
  typedValue: string,
  stakingAmount: TokenAmount
): {
  parsedAmount?: CurrencyAmount
  error?: string
} {
  const { account } = useActiveWeb3React()

  const parsedInput: CurrencyAmount | undefined = tryParseAmount(typedValue, stakingAmount.token)

  const parsedAmount = parsedInput && JSBI.lessThanOrEqual(parsedInput.raw, stakingAmount.raw) ? parsedInput : undefined

  let error: string | undefined
  if (!account) {
    error = 'Connect Wallet'
  }
  if (!parsedAmount) {
    error = error ?? 'Enter an amount'
  }

  return {
    parsedAmount,
    error
  }
}
